// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'



/*  mint-ui  axios*/
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css';
Vue.use(Mint);

import axios from 'axios';
import VueAxios from 'vue-axios';


//css 
import './assets/css/base.css';
import './assets/css/normalize.css'
import './assets/js/rem';

import cookie from 'js-cookie'

// import common from './api/common'
// import config from './api/config'

Vue.prototype.common=common;
Vue.prototype.config=config;

import receiveData from './api/receiveData'
Vue.prototype.receiveData=receiveData


//axios
var axiosInstance = axios.create({
  transformRequest: [function (data) {
      //data = Qs.stringify(data);
      data = JSON.stringify(data);
      return data;
  }],
  headers: {
      'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
      // 'Content-Type':'application/json;charset=utf-8',
      'Content-Type':"application/json",

      // 'Accept': '*/*',
      'Accept': 'application/json',

  },
  baseURL: Vue.prototype.config.URL,
  withCredentials:true,
  transformResponse: [function (data) {//
    return data;
  }],
});
//axios cookie

axiosInstance.interceptors.request.use(
  config => {
      if( !cookie.get('session')){//seesion   seesion
        
      }else{// session
        config.headers.Cookie =`${ cookie.get('Cookie') }`
      }

      // config.headers.Authorization = // token
      //     `${ Cookies.get('yesmywine_mall$token_type') } ${ Cookies.get('yesmywine_mall$token') }`
       //  return config http
       return config
  },
  err => {
      return Promise.reject(err)
  }
)
Vue.use(VueAxios, axiosInstance);


Vue.config.productionTip = false
Vue.config.debug=true;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

