import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import index from '@/pages/index/index'//
import Bindphone from '@/pages/bindphone'//
import Register from '@/pages/register'//
import Coupons from '@/pages/coupons'//
import Myrepair from '@/pages/myRepair/myrepair'//
import OrdersDetail from '@/pages/myRepair/ordersDetail'//
import cancelOrders from '@/pages/myRepair/cancelOrders'//
import OperatorOrders from '@/pages/myRepair/operatorOrders'//
import OperatorOrdersDetail from '@/pages/myRepair/operatorOrdersDetail'//
import OperatorRepairSuccess from '@/pages/myRepair/operatorRepairSuccess'// 
import Notices from '@/pages/notices'//
import MyPublish from '@/pages/communities/myPublish'//
import ThreadDetail from '@/pages/communities/threadDetail'//
import Addresses from '@/pages/addresses'//
import Abort from '@/pages/abort'//

// import common  from '../api/common'

Vue.use(Router)

let router= new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      children:[
        {path:'',component:index,meta: {title:''}}
      ]
    },
    //
    {
      path:'',
      name:'index',
      component:index,
      meta: {
        title:''
      }
    },
    {
      path:'/register',
      name:'register',
      component:Register,
      meta: {
        title:''
      }
    },
    {
      path:'/bindphone',
      name:'bindphone',
      component:Bindphone,
      meta: {
        title:''
      }
    },
    {
      path:'/coupons',
      name:'coupons',
      component:Coupons,
      meta:{
        title:''
      }
    }, 
    {
      path:'/myrepair',
      name:'myrepair',
      component:Myrepair,
      meta: {
        title:''
      }
    },
    {
      path:'/ordersDetail',
      name:'ordersDetail',
      component:OrdersDetail,
      meta: {
        title:''
      }
    },
    {
      path:'/cancelorders',
      name:'cancelorders',
      component:cancelOrders,
      meta: {
        title:''
      }
    },
    {
      path:'/notices',
      name:'notices',
      component:Notices,
      meta: {
        title:''
      }
    },
    {
      path:'/myPublish',
      name:'myPublish',
      component:MyPublish,
      meta: {
        title:''
      }
    },
    {
      path:'/threadDetail',
      name:'threadDetail',
      component:ThreadDetail,
     
    },
    {
      path:'/addresses',
      name:'addresses',
      component:Addresses,
      meta:{
        title:''
      }
    },
    {
      path:'/abort',
      name:'abort',
      component:Abort,
      meta:{
        title:''
      }
    },
    {
      path:'/operatorOrders',
      name:'operatorOrders',
      component:OperatorOrders,
      meta: {
        title:''
      }
    },
    {
      path:'/operatorOrdersDetail',
      name:'operatorOrdersDetail',
      component:OperatorOrdersDetail,
    
    },
    {
      path:'/operatorRepairSuccess',
      name:'operatorRepairSuccess',
      component:OperatorRepairSuccess,
    },
  ]
})

////
// to  from next() 

router.beforeEach((to, from, next) => {
  //title
  if(to.matched[0].name !== "index") {
    
    common.checkRegisterStatus()
  }

  changeTitle(to.meta.title);
  next();
});

//title
function changeTitle(title) {
  title = title ? title : '';
  window.document.title = title;
};


export default router



